@import "../utilities/variables";

/* ====================== */
/* BASE STYLLES */
/* ====================== */
* {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  min-height: 1280px;
  color: #3b3b3b;
  font-size: 24px;
  background-color: $bg-color-right; //$bg-color;
}

p.intro-text {
  font-size: 1.1rem;
  text-align: left;
  font-family: $main-font;
  color: white;
}

p,
h1,
h2,
h3,
h4,
a {
  margin: 0;
  font-weight: 400;
}

a:visited,
a:hover {
  // color: $color;
  text-decoration: none;
  // border-bottom: 2px solid currentColor;
}

a {
  color: $squid-ink;
  text-decoration: underline;
}

a:hover {
  color: $color-right;
  background-color: $bg-color-left;
  text-decoration: none;
  // border-bottom: 2px solid currentColor;
}

#intro {
  max-width: 45rem;
  margin: 1rem auto;
  text-align: center;
}

.cardSpan {
  cursor: pointer;
}

#toc {
  padding-left: 4rem;
}

#intro__icon h2 {
  // padding-left: 2%;
  font-family: AmazonEmberDisplayHeavy;
  font-size: 1rem;
}

.text {
  h2 {
    font-size: 1.5rem;
    padding-left: 1px;
    font-family: AmazonEmberDisplayHeavy;
  }
}

.icon,
.text {
  vertical-align: middle;
  display: inline-block;
}
/* ====================== */
/* LAYOUT STYLES */
/* ====================== */

#ai {
  color: $ai-color;
  font-family: AmazonEmberDisplayHeavy;
}

.logo {
  color: $color-left;
  width: 10rem;
}

#intro-icon {
  display: flex;
  // justify-content: center;
  // vertical-align: center;
  text-transform: uppercase;
  // letter-spacing: 1.5px;
  // padding: 2rem;
  font-weight: bold;
  color: $color-left;
  flex-direction: row;
}

#intro-icon h2 {
  padding-left: 2%;
  font-family: AmazonEmberDisplayHeavy;
  font-size: 1.2rem;
  margin-top: 10px;
  margin-bottom: 10px;
}

.section-title {
  font-family: AmazonEmberDisplayHeavy;
  font-size: 2.2rem;
  text-align: center;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

section {
  min-height: 100vh;
  // max-height: 100vh;
  vertical-align: middle;
  text-align: center;
  align-items: center;
  justify-content: center;
  // height: 100vh;
}

section {
  margin-bottom: 24vh;
  h2 {
    padding-top: 20vh;
    font-size: 2.5rem;
    color: $color-left;
    font-family: $font-heavy;
  }
  h3 {
    font-size: 1.5rem;
    padding-top: 1rem;
    font-family: AmazonEmberDisplayHeavy;
    color: $color-left;
    opacity: 0.9;
  }
}

section p {
  text-align: center;
  justify-content: center;
  align-items: middle;
  vertical-align: middle;
  text-align: center;
  align-items: center;
  justify-content: center;
}

#intro-icon svg {
  color: black;
  fill: black;
}

#scrolly > * {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
}

#scrolly {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

article {
  position: relative;
  padding: 0 1.5rem;
  vertical-align: middle;
  text-align: center;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
  margin: 0 auto;
  max-width: 34%;
  color: $color-left;
  background-color: $bg-color-left;

  p {
    font-size: 1.15rem;
    margin: 4px;
    padding: 1rem;
    padding-top: 2rem;
    justify-content: center;
    text-align: center;
    // border: 2px solid black;
    // box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.3);
    background-color: $bg-color-left;
  }

  h2 {
    background-color: $bg-color-left;
    font-family: $font-light;
    display: inline-block;
    padding: 0px;
    margin: 0px;
    color: $color-left;
  }

  h3 {
    display: none;
  }

  #end-p {
    display: none;
  }
}

figure {
  position: -webkit-sticky;
  position: sticky;
  height: 90vh;
  width: 100%;
  margin: 0;
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  z-index: 0;
  vertical-align: center;
  top: 6rem;
  justify-content: center;
  text-align: center;
}

#all {
  margin-bottom: 15vh;
}

section {
  // margin-bottom: 50vh;
  // margin-top: 10vh;

  h2 {
    padding-top: 0vh;
  }
}
// new
#title {
  background-color: $bg-color-left;
  display: flex;
  align-items: center;
  text-align: center;
  flex-direction: column;
  min-width: 35%;
  min-height: 110vh;

  h1 {
    color: $color-left;
    font-family: $font-heavy;
    font-size: 5rem;
    padding: 1rem;
  }

  p {
    color: $color-left;
    font-family: $font-heavy;
    opacity: 0.9;
    font-size: 1.45rem;
  }

  a,
  a:hover {
    color: $color-left;
    background-color: $bg-color-left;
    text-decoration: underline;
    font-size: 1.4rem;
  }

  #subtitle {
    color: $color-left;
    font-family: $font-light;
    font-size: 1.4rem;
    padding: 1rem;
    opacity: 0.8;
  }
}

section#intro-text {
  font-size: 1rem;
  padding: 5rem;
  background-color: $bg-color-right; //$ai-color;
  min-height: 100vh;
  padding-top: 5rem;
  color: $color-right;

  h2 {
    font-size: 1.6rem;
    font-family: $font-light;
    padding-bottom: 1rem;
  }

  p {
    padding: 1rem;
    font-size: 1.2rem;
  }
}

#chart,
#chart2 {
  // outline: 2px solid red;
  // height: 36vh;
  width: 100%;
  margin: 0 auto;
}

#chart-wrapper {
  // grid for plots
  display: grid;
  // grid-template-columns: repeat(1, 75%);
  grid-template-columns: 40% 55%;
  grid-row-gap: 2rem;
  grid-column-gap: 1rem;
  grid-template-rows: 60% 40%;
  justify-content: center;
  height: 90vh;
}

#train {
  margin-bottom: 25vh;
}

// new
#main-intro {
  display: flex;
  text-align: center;
  min-height: 110vh;
}

#splits {
  text-align: center;
  padding: 5rem;
  background-color: $bg-color-left;
  color: $color-left;
  margin-bottom: 0;

  h2 {
    font-size: 2.5rem;
    font-family: $font-light;
    padding-bottom: 1rem;
    color: $color-left;
  }

  p {
    padding: 1rem;
    font-size: 1.2rem;
    margin: 0 auto;
  }

  a:hover {
    color: $color-left;
    background-color: $bg-color-right;
    text-decoration: none;
    // border-bottom: 2px solid currentColor;
  }
}


#variance {
  a:hover {
    color: $color-right;
    background-color: $bg-color-right;
    text-decoration: none;
    // border-bottom: 2px solid currentColor;
  }
}

#limitations {
  text-align: center;
  padding: 5rem;
  background-color: $bg-color-left;
  color: $color-left;
  margin-bottom: 0;

  h2 {
    font-size: 2.5rem;
    font-family: $font-light;
    padding-bottom: 1rem;
    color: $color-left;
  }

  p {
    padding: 1rem;
    font-size: 1.2rem;
    max-width: 70%;
    margin: 0 auto;
  }

  a:hover {
    color: $color-right;
    background-color: $bg-color-right;
    text-decoration: none;
    // border-bottom: 2px solid currentColor;
  }
}

#final {
  text-align: left;
  padding: 1rem;
  background-color: $bg-color-right;
  color: $color-left;
  margin: 0 auto;

  p {
    text-align: left;
    padding: 1rem;
    font-size: 1.2rem;
    max-width: 35%;
    margin: 0 auto;
    margin-bottom: 0;
    color: $color-bottom;
    line-height: 2;
  }
}

// utils
.bold {
  font-weight: bold;
  text-align: left;
  font-family: $font-heavy;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

// mew

article {
  background-color: $bg-color-left;
}

.annotation-note {
  font-family: $font-mono;
  font-size: 0.9rem;
  fill: $color-right;
}

.annotation-note-label {
  font-family: $font-light;
  font-size: 0.9rem;
  fill: $color-right;
}

.annotation-note-title {
  font-family: $font-light;
  font-size: 1rem;
  fill: $color-right;
}

.connector {
  fill: $color-right;
  stroke-dasharray: (4, 4);
}

#intro-tree-chart {
  display: flex;
  justify-content: center;
}

article {
  section:nth-child(n + 2) {
    padding: 4rem;
    p {
      text-align: left;
    }
  }
}

section#intro-text,
#title-section {
  padding-bottom: 25rem; // distance below intro text - make responsive
}

@media (max-height: 600px) {
  section#intro-text,
  #title-section {
    padding-bottom: 40rem; // distance below intro text - make responsive
  }
}
