/* ====================== */
/* 
    Styles for pertubation small multiples section 
*/
/* ====================== */
@use 'utilities/fonts';
@import "../utilities/variables";

#pertubations {
  text-align: center;
  padding: 5rem;
  background-color: $bg-color-right;
  color: $color-right;
  margin-bottom: 0;

  h2 {
    font-size: 2.5rem;
    font-family: $font-light;
    padding-bottom: 1rem;
    color: $color-right;
  }

  p {
    padding: 1rem;
    font-size: 1.2rem;
  }
}

#pertubation-wrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 20%);
  gap: 2rem 1rem;
  min-height: 110vh;
  width: 85%;
  margin: auto;
  max-width: 75rem;
}

.pertubation-text {
  font-family: $font-heavy;
  stroke-linejoin: round;
  fill: white;
  paint-order: stroke fill;
  stroke: $bg-color-right;
  stroke-width: 3px;
  font-size: 0.6rem;
}

// .pertubation-item {
