//     justify-content: center;
//     align-items: center;
// }

// Entropy
.subheader {
  text-align: left;
  font-size: 2rem;
}

#entropy-chart {
  min-height: 70vh;
  width: 95%;
  max-width: 72rem;
  margin: auto;
  padding-bottom: 1rem;
}

#entropy-chart-svg {
  // outline: 2px solid black;
}

#entropy-buttons {
  padding: 0.5rem;
  display: flex;
  margin: auto;
  justify-content: center;
}

#entropy-buttons > button {
  margin-right: 1rem;
}

.entropy-class-label {
  fill: white;
  font-family: $font-heavy;
  font-size: 1.1rem;
  stroke-linejoin: round;
  fill: white;
  paint-order: stroke fill;
  stroke: $bg-color-right;
  stroke-width: 3px;
}

.entropy-x-axis {
  fill: white;
  font-size: 1rem;
  font-family: $font-heavy;
  stroke-linejoin: round;
  fill: white;
  paint-order: stroke fill;
  stroke: $bg-color-right;
  stroke-width: 3px;
  text-align: left;
}

.center-label {
  fill: white;
  font-size: 1.2rem;
  font-family: $font-heavy;
  stroke-linejoin: round;
  fill: white;
  paint-order: stroke fill;
  stroke: $bg-color-right;
  stroke-width: 3px;
  text-align: left;
}

#entropy-x-axis,
#entropy-y-axis {
  opacity: 0.9;
  text {
    font-family: $font-heavy;
    font-size: 0.8rem;
    opacity: 0.8;
    stroke-linejoin: round;
    fill: white;
    paint-order: stroke fill;
    stroke: $bg-color-right;
    stroke-width: 1px;
    text-align: left;
    // hmm
    font-size: 0.85rem;
    font-family: $font-heavy;
    stroke-linejoin: round;
    fill: white;
    paint-order: stroke fill;
    stroke: $bg-color-right;
    stroke-width: 3px;
    text-align: left;
  }
  line {
    opacity: 0.15;
  }
}

.entropy-button-text {
  fill: white;
  font-family: $font-heavy;
  font-size: 0.9rem;
  pointer-events: none;
  stroke-linejoin: round;
  // fill: $bg-color-left;
  // paint-order: stroke fill;
  // stroke: white;
  // stroke-width: 3px;
  // text-align: left;
}

.entropy-button-rect {
  stroke: white;
  stroke-width: 2px;
  fill: rgb(85, 85, 85);
  // fill: $bg-color-right;
}

.entropy-button-rect:hover {
  stroke: $squid-ink;
}

.entropy-button-rect:checked {
  stroke: $bg-color-right;
}
