/* ====================== */
/* 
    Styles for first scrolly section 
    e.g, the side-by-side decision tree and scatter plot.
*/
/* ====================== */
@use 'utilities/fonts';
@import "../utilities/variables";

/* ====================== */
/* TREE STYLLES */
/* ====================== */
.dt-text {
  font-family: $font-heavy;
  stroke-linejoin: round;
  fill: white;
  paint-order: stroke fill;
  stroke: $bg-color-right;
  stroke-width: 3px;
  font-size: 0.85rem;
}

.dt-scatter-legend {
  font-family: $font-heavy;
  font-size: 0.8rem;
  stroke-linejoin: round;
  fill: white;
  paint-order: stroke fill;
  stroke: $bg-color-right;
  stroke-width: 3px;
}

#dt-scatter-y-axis,
#dt-scatter-x-axis {
  font-family: $font-heavy;
  fill: white;
  color: white;
}

.scatter-axis-text {
  font-family: $font-heavy;
  fill: white;
  font-size: 1rem;
  color: white;
}
